.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

@keyframes stopwatch {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

.minutes-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite stopwatch;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

.seconds-hand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite stopwatch;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch--speed-up .minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .seconds-hand {
  animation-duration: 10s;
}
